.root {
    width: 295px;
    padding: 12px 22px;
    color: #333333;
    border-right: 1px solid rgba(229, 231, 235, 1);
}

.title {
    padding: 12px 12px 0;
}

.item {
    width: 100%;
    display: flex;
    align-items: center;
    height: 50px;
    border-radius: 10px;
    transition: 0.3s;
    cursor: pointer;
}

.item__image {
    display: flex;
    margin-right: 10px;
    width: 40px;
    height: 40px;
}

.item__avatar {
    border-radius: 50%;
}

.userAndMessage {
    display: flex;
    flex-direction: column;
    margin-left: 5px;
}

.item__userName {
    font-size: 15px;
    line-height: 23px;
    font-weight: 600;
    color: #5d4cc9;
}
a {
    text-decoration: none;
}

.userMessage {
    font-size: 14px;
    line-height: 26px;
    color: #888;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}

.item__link {
    display: flex;
    align-items: center;
    color: #585757;
    list-style: none;
}

.item__link_active {
    display: flex;
    height: 45px;
    width: 100%;
    align-items: center;
    background-color: #2a41e812;
    color: #9397ee;
    border-radius: 10px;
    transition: 0.3s;
    text-decoration: none;
}
